// @flow
import * as React from 'react';
import {
  AppBar,
  Toolbar,
  makeStyles,
  Container,
  List,
  ListItem,
  ListItemIcon,
  ListItemText,
} from '@material-ui/core';
import { ShowChart, House, FileCopy, CalendarToday } from '@material-ui/icons';
import _get from 'lodash/get';

import { Auth } from 'aws-amplify';
import imageLogo from '../../images/logo_n.svg';

const useStyles = makeStyles(theme => ({
  header: {
    background: theme.palette.common.white,
    minHeight: '83px',
  },
  menu: {
    padding: '0px',
  },
  menuItem: {
    float: 'left',
    width: 'auto',
    padding: '20px 0px',
    margin: '0px 20px 0px 0px',
    '&:hover, &:focus': {
      borderBottom: `2px solid ${theme.palette.common.black}`,
    },
  },
  menuIcon: {
    minWidth: '30px',
  },
  menuItemText: {
    fontFamily: theme.typography.fontFamily.avenirLT,
    textTransform: 'uppercase',
    fontWeight: 900,
    fontSize: '0.625rem',
    lineHeight: '0.75rem',
    letterSpacing: '1.54px',
    margin: '0px',
  },
  menuItemLink: {
    '&:hover, &:focus': {
      background: 'none',
      '.MuiSvgIcon-root': {
        color: 'red',
      },
    },
  },
  menuUser: {
    marginLeft: 'auto',
  },
  menuUserSignOutBtn: {
    width: '100px',
    textAlign: 'center',
    marginLeft: '30px',
  },
}));

function ListItemLink(props) {
  const classes = useStyles();
  return (
    <ListItem
      className={classes.menuItemLink}
      disableGutters={true}
      button
      component="a"
      {...props}
    />
  );
}

const Header = (): React.Element<typeof Container> => {
  const classes = useStyles();
  const isLoggedIn = Auth.user !== null;
  const email = _get(Auth, ['user', 'attributes', 'email'], '');
  const links = [
    ['DashBoard', <ShowChart key="chart" />, '/'],
    ['My Properties', <House key="house" />, '#'],
    ['Offers', <FileCopy key="copy" />, '#'],
    ['Calendar', <CalendarToday key="calendar" />, '#'],
  ];

  const renderLinks = () => {
    return links.map((item, key) => {
      const [name, Icon, link] = item;
      return (
        <ListItem className={classes.menuItem} key={key}>
          <ListItemIcon className={classes.menuIcon}>{Icon}</ListItemIcon>
          <ListItemLink href={link}>
            <ListItemText
              primary={<span className={classes.menuItemText}>{name}</span>}
            />
          </ListItemLink>
        </ListItem>
      );
    });
  };

  const onClickSignOutHandler = async () => {
    await Auth.signOut();
  };

  return (
    <Container maxWidth="xl">
      {isLoggedIn ? (
        <AppBar position="static" color="default" className={classes.header}>
          <Toolbar>
            <div className={classes.logo}>
              <img src={imageLogo} width="100%" alt="LogoW" />
            </div>
            <List component="ul" className={classes.menu}>
              {renderLinks()}
            </List>
            <List component="ul" className={classes.menuUser}>
              <ListItem component="li">
                <ListItemText primary={email} />
                <ListItemLink
                  href="/"
                  className={classes.menuUserSignOutBtn}
                  onClick={onClickSignOutHandler}
                >
                  <ListItemText primary="Sign Out" />
                </ListItemLink>
              </ListItem>
            </List>
          </Toolbar>
        </AppBar>
      ) : (
        <header className="top_header desktop">
          <div className="menu">
            <a href="/" className="menu_btn">
              Home
            </a>
            <a href="/about" className="menu_btn">
              About
            </a>
            <a href="/how-it-works" className="menu_btn">
              How It Works
            </a>
            <a href="/search" className="menu_btn">
              Search
            </a>
            <a href="/soldhomes" className="menu_btn">
              Sold Homes
            </a>
            <a href="/contact" className="menu_btn">
              Contact Us
            </a>
            <a href="/media" className="menu_btn">
              Media
            </a>
          </div>
        </header>
      )}
    </Container>
  );
};

export default React.memo(Header);
